/* General Styles */

.repo-container {
  margin-top: 5%;
  font-family: Arial, sans-serif;
  padding: 20px;
  /* background-color: #fffefe; */
  /* height: auto; */
  font-size: 40px;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  align-items: auto;
}

.button-container {
  display: flex;
  justify-content: space-between;
}


.dir-heading {
  margin-top: 0;
  /* margin-bottom: 10%; */
  color: #333;
  font-style: bold;
  font-size: 3rem;
  font-weight: 700;
  /* only wrap dir items contained in this div, don't break up text */
  display: flex;
  flex-wrap: wrap;
}

.dir-item {
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

/* Directory Styles */

.directory-list {
  max-width: 80%;
  display: flex;
  flex-wrap: wrap;
}

.directory-item,
.file-item {
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.directory-item:hover,
.file-item:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.directory-button,
.file-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.8rem;
  transition: color 0.3s ease;
}

.directory-button:hover,
.file-button:hover {
  color: #007bff;
}

.directory-button {
  color: blue;
  font-weight: 900;
}

.file-name {
  /* margin-left: 10px; */
  font-style: italic;
  color: #000000;
}

.close-file {
  width:max-content;
  background-color: #767676;
  color: #fff;
  border: none;
  padding: .5rem .75rem;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 4%;
  margin-left: 5%;
}

.close-file:hover {
  background-color: #cc0000;
}
