@media (max-width: 768px) {
  .dir-heading {
    font-size: 10vw;
  }

  .file-name,
  .directory-button {
    font-size: 5vw;
    /* white-space: nowrap; */
  }

  .directory-item,
  .file-item {
    margin: 1px;
    padding: 1px;
  }
}
